// Кастомный инпут type file - начало -
const initCustomInputTypeFile = () => {
  let inputs = document.querySelectorAll('.input-type-file');
  inputs.forEach(function (input) {
    let label = input.nextElementSibling;
    let labelVal = label.querySelector('span').innerText;
    input.addEventListener('change', function (e) {
      let countFiles = '';
      if (this.files && this.files.length >= 1) {
        countFiles = this.files.length;
      }
      if (countFiles) {
        label.querySelector('span').innerText = 'Выбрано файлов: ' + countFiles;
      } else {
        label.querySelector('span').innerText = labelVal;
      }
    });
  });
};
export {initCustomInputTypeFile};
// Кастомный инпут type file - конец -
