import {iosVhFix} from './utils/ios-vh-fix';

import {initModals} from './modules/modals/init-modals';
import {initTabs} from './modules/init-tabs';
import {initSwitchOnLogInModal} from './modules/switch-on-login-in-modal';
import './modules/custom-select';
import './modules/init-form-validate';
import {initMobileMenu} from './modules/init-mobile-menu';
import {initChangeOption} from './modules/change-options';
import {initHideInstruction} from './modules/hide-instructions';
import {initCustomInputTypeFile} from './modules/custom-input-type-file';

// ---------------------------------

window.addEventListener('DOMContentLoaded', () => {
  // Utils
  // ---------------------------------

  // только если ie11
  // ieFix();
  // https://github.com/fregante/object-fit-images
  // polyfillObjectFit();

  iosVhFix();

  // Modules
  // ---------------------------------
  initHideInstruction();
  // все скрипты должны быть в обработчике 'DOMContentLoaded', но не все в 'load'
  // в load следует добавить скрипты, не участвующие в работе первого экрана
  window.addEventListener('load', () => {

    initModals();
    initTabs();
    initSwitchOnLogInModal();
    initMobileMenu();
    initChangeOption();
    initCustomInputTypeFile();
  });
});

